
const FastClick = require('fastclick');
FastClick.attach(document.body);
import tools from '../../lib/tools.js';
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import value2name from 'vux/src/filters/value2name.js'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'editAddress',
components:{Cell,XInput,XTextarea,XAddress,Toast},
data(){
return {
id:'',
debrisOne:true,
addressData:ChinaAddressV3Data,
info:{},
name:'',
mobile:'',
address:[],
zipCode:'',
detailedAddress:'',
showToast:false,
toastMsg:'',
allAddress:''
}
},
created(){
this.loading();
},
methods:{
toggleView(){
this.debrisOne = false;
},
loading(){
var vm = this;

let id = this.id = tools.getRequestParam('md') || localStorage.getItem('md') || tools.getCookie('user_id');
this.$http.post('/apiUser/queryAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
if(vm.info.address){
let address = JSON.parse(vm.info.address);
console.log('editaddress==',address);
vm.name = address.name;
vm.mobile = address.mobile;
vm.address = address.address && address.address.split(' ');
vm.zipCode = address.zipCode;
vm.detailedAddress = address.detailedAddress;
vm.allAddress = address.address + ' ' + address.detailedAddress + ' ' + address.zipCode;
sessionStorage.setItem('tempAddress',vm.info.address);
}
}
}else{
vm.showToast = true;
vm.toastMsg = '';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
editing(){
var vm = this;

this.id = tools.getRequestParam('md') || localStorage.getItem('md') || tools.getCookie('user_id');
if(!this.id){return}
let data = {managerId:vm.id};
if(!(this.name && this.mobile && this.address && this.zipCode && this.detailedAddress)){
vm.showToast = true;
vm.toastMsg = ''
return ;
}
data.address = JSON.stringify({name:this.name,mobile:this.mobile,address:value2name(this.address, ChinaAddressV3Data),zipCode:this.zipCode,detailedAddress:this.detailedAddress});

console.log('data===',data);
let tempAddress = data.address;
this.$http.axios({method:'POST',url:'/apiUser/update',data:data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.showToast = true;
vm.toastMsg = '';
sessionStorage.setItem('tempAddress',tempAddress);
tools.next(sessionStorage.getItem('tempURL'));
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
})
}
}
}



